/* Works on Chrome, Edge, and Safari */
[data-theme="classic"] *::-webkit-scrollbar {
	width: 6px;
}
[data-theme="classic"] *::-webkit-scrollbar-track {
	background: var(--scroll-bg);
}
[data-theme="classic"] *::-webkit-scrollbar-thumb {
	background-color: var(--scroll-color);
	border-radius: 20px;
}
[data-theme="classic"] * {
	scrollbar-width: thin;
	scrollbar-color: var(--scroll-color) var(--scroll-bg);
}
[data-theme="classic"] .awesomplete .input-with-feedback {
	border: 1px solid var(--border-color);
}
[data-theme="classic"] .form-control {
	border: 1px solid var(--border-color);
}
[data-theme="classic"] .search-bar .awesomplete input {
	background-color: var(--bg-color);
}
[data-theme="classic"] .modal-backdrop {
	background-color: #2c436b !important;
}
[data-theme="classic"] .widget .widget-head .widget-title {
	font-size: var(--text-base);
}
[data-theme="classic"] .comment-box .comment-input-header,
[data-theme="classic"] .form-dashboard-section .section-head,
[data-theme="classic"] .form-section .section-head,
[data-theme="classic"] .head-title {
	font-size: var(--text-base);
	font-weight: 600;
}
[data-theme="classic"] .comment-box .comment-input-container .ql-editor {
	border: 1px solid var(--border-color);
}
[data-theme="classic"] .duration-picker .duration-input {
	background-color: var(--bg-color);
	border: 1px solid var(--border-color);
}
[data-theme="classic"] .control-label {
	font-weight: 500;
}
[data-theme="classic"][data-theme="dark"] {
	--bg-color: var(--gray-900);
	--disabled-control-bg: var(--gray-900);
	--border-color: #1c2126;
	--margin-lg: 15px;
	--text-bold: 600;
	--scroll-bg: #161a1f;
	--scroll-color: #1c2126;
}
[data-theme="classic"][data-theme="light"] {
	--bg-color: #f2f5fa;
	--text-bold: 600;
	--control-bg: #fff;
	--control-bg-on-gray: #d4dcea;
	--disabled-control-bg: #f2f5fa;
	--heading-color: #333;
	--border-color: #e3e8f1;
	--avatar-frame-bg: #dde2ea;
	--margin-lg: 15px;
	--sidebar-select-color: #d4dcea;
	--awesomplete-hover-bg: #d4dcea;
	--scroll-bg: #eef1f5;
	--scroll-color: #d9dfe6;
}